// –û—Ç–∫–ª—é—á–∞–µ—Ç –ø–µ—Ä–µ—Ö–æ–¥—ã –ø–æ –¥–µ–º–æ-—Å—Å—ã–ª–∫–∞–º (–Ω–∞–ø—Ä–∏–º–µ—Ä, href="#" –∏–ª–∏ href="#!")
export function disableDemoLinks() {
	document.addEventListener("click", (e) => {
		const link = e.target.closest("a");
		if (!link) return;

		const href = link.getAttribute("href");

		// –ò–≥–Ω–æ—Ä–∏—Ä—É–µ–º –ø—É—Å—Ç—ã–µ –∏–ª–∏ —Ñ–µ–π–∫–æ–≤—ã–µ —Å—Å—ã–ª–∫–∏
		if (href === "#" || href === "#!" || href === "" || href === "javascript:void(0)") {
			e.preventDefault();
			e.stopPropagation();

			// –î–ª—è –Ω–∞–≥–ª—è–¥–Ω–æ—Å—Ç–∏ –º–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å –Ω–µ–±–æ–ª—å—à–æ–π –≤–∏–∑—É–∞–ª—å–Ω—ã–π —ç—Ñ—Ñ–µ–∫—Ç (–ø–æ –∂–µ–ª–∞–Ω–∏—é)
			link.style.transition = "opacity 0.15s ease";
			link.style.opacity = "0.6";
			setTimeout(() => (link.style.opacity = "1"), 150);
		}
	});
}
